
.app-img {
  height: 20px;
  width: 20px;
  filter: invert(100%) sepia(2%) saturate(714%) hue-rotate(318deg) brightness(88%) contrast(94%);

  top: 10px;
  margin-top: 3px;
  margin-bottom: 0px;
  margin-left: 4px;
  margin-right: 4px;

  pointer-events:none;
}

.flip-arrow-left-wrapper,
.flip-arrow-right-wrapper {

  position: absolute;
  text-align: center;
  top: 180px;
  z-index: 2;
  opacity: 0;

  border-radius: 100px;
  border-color: #e1e1e1;
  border-style: solid;
  border-width: 5px;
  border-spacing: 0px;

  pointer-events:none;
}

.flip-arrow-left-wrapper {
  left: 10px;
}

.flip-arrow-right-wrapper {
  transform: scaleX(-1);

  left: 595px;
}
