.darkmode-back {
  background-color: #161616;
  border-radius: 43px;
  height: 382px;
  width: 640px;
  overflow: hidden;
  transform-style: preserve-3d;
}
/* 
.darkmode-back:hover {
  border-color: #4e52b1;
  border-style: solid;
  border-width: 2px;
} */

.darkmode-back .overlap-group {
  background-color: #161616;
  border-bottom-style: solid;
  border-bottom-width: 4px;
  border-color: #4e52b1;
  border-left-style: solid;
  border-left-width: 17px;
  border-radius: 47px;
  height: 327px;
  left: 30px;
  position: relative;
  top: 29px;
  width: 581px;
}

.darkmode-back .overlap {
  height: 48px;
  left: 28px;
  position: absolute;
  top: 271px;
  width: 524px;
}

.darkmode-back .SAMUELSON-PAJEU {
  color: #e1e1e1;
  font-family: "Orbitron", Helvetica;
  font-size: 13px;
  font-weight: 700;
  left: 0;
  letter-spacing: 6.11px;
  line-height: normal;
  position: absolute;
  text-align: center;
  top: 2px;
  width: 524px;
  cursor: default;
}

.darkmode-back .text-wrapper {
  color: #4e52b1;
  font-family: "Orbitron", Helvetica;
  font-size: 16px;
  font-weight: 700;
  left: 288px;
  letter-spacing: 7.52px;
  line-height: normal;
  position: absolute;
  text-align: center;
  top: 0;
  width: 54px;
  overflow: hidden;
  cursor: default;
}

.darkmode-back .rectangle {
  background-image: url(../../../public/assets/img/vector-2.svg);
  background-size: 100% 100%;
  height: 120px;
  left: 67px;
  position: absolute;
  top: 25px;
  width: 120px;
  overflow: hidden;
}

.darkmode-back .git-hub-wrapper {
  background-image: url(../../../public/assets/img/vector-2.svg);
  background-size: 100% 100%;
  height: 120px;
  left: 239px;
  position: absolute;
  top: 25px;
  width: 120px;
  overflow: hidden;
}



.darkmode-back .img {
  height: 90px;
  left: 15px;
  position: absolute;
  top: 15px;
  width: 90px;
  overflow: hidden;
  transition: width 0.2s, height  0.2s, top  0.2s, left  0.2s;
}

.darkmode-back .img:hover {
  cursor: pointer;
  left: 10px;
  top: 10px;
  height: 100px;
  width: 100px;
  filter: invert(25%) sepia(69%) saturate(1150%) hue-rotate(213deg) brightness(92%) contrast(87%);
}

.darkmode-back .qr-code-wrapper {
  background-image: url(../../../public/assets/img/vector-2.svg);
  background-size: 100% 100%;
  height: 120px;
  left: 410px;
  position: absolute;
  top: 25px;
  width: 120px;
}

.test-light {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255,255,252, 0.2);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 1;
  transition: width 0.4s, height 0.4s;
  box-shadow: 0 0 2em rgba(255, 255, 255, 0.2);
  filter: blur(2em)
}